















































import Vue from "vue";
import Header from "@/components/Header.vue";
import { mapState } from "vuex";
import store from "@/store/index";
import { mdiPlus } from "@mdi/js";
export default Vue.extend({
  name: "Fiche",
  components: {
    Header,
  },
  data() {
    return {
      who: undefined,
      show: false,
      show1: false,
      show2: false,
      avatarImg: require("../assets/images/avatar1.png"),
      liveImg: require("../assets/images/live.svg"),
      plusIcon: mdiPlus,
    } as any;
  },
  computed: mapState({
    getUser: (state: any) => state.currentUser,
    getFiche: (state: any) => state.fiche,
  }),
  mounted() {
    store.dispatch("LOAD_CURRENT_USER", this.who);
    store.dispatch("LOAD_FICHE", this.who);
    this.$refs.videoRef.src =
      "http://iandevlin.github.io/mdn/video-player/video/tears-of-steel-battle-clip-medium.mp4";
  },
  created() {
    const SegmentStr = window.location.pathname;
    const segmentArray = SegmentStr.split("/");
    const lastSegment = segmentArray.pop();
    this.who = lastSegment;
  },
    methods: {
    getImage(path) {
      return require("../assets/images/avatar/" + path);
    },
  }
});
