<template>
  <div class="home">
    <div class="home-content">
      <h2 class="text-uppercase-medi">Les supers profs</h2>
      <div class="pillules">
        <div
          v-for="user in getUsers"
          :key="user.id"
          width="100%"
          class="rounded-pill myCard"
          color="white"
          elevation="6"
        >
          <v-col>
            <v-row class="pl-4 line">
              <v-avatar class="avatar">
                <img :src="getImage(user.avatar)" />
              </v-avatar>
              <div class="textContent">
                <p>
                  <span class="nom-influ">{{ user.name }} <br /></span>
                  <span class="id-influ">{{ user.atId }}</span>
                </p>
              </div>
              <div class="circleContainer">
                <router-link
                  tag="div"
                  style="cursor: pointer"
