<script>
// @ is an alias to /src
import Vue from "vue";
import Footer from "@/components/Footer.vue";
import store from "../store/index";
import { mapState } from "vuex";

export default Vue.extend({
  name: "Home",
  components: {
    Footer,
  },
  data() {
    return {
      avatarImg: require(`../assets/images/avatar1.png`),
      arrowInflu: require("../assets/images/chevron-right-solid.svg"),
      test: "",
      who: undefined,
    };
  },
  computed: mapState({
    getUsers: (state) => state.allUser,
  }),
  mounted() {
    store.dispatch("LOAD_USERS", "users");
  },
  created() {
    const SegmentStr = window.location.pathname;
    const segmentArray = SegmentStr.split("/");
    const lastSegment = segmentArray.pop();
    this.who = lastSegment;
  },
  methods: {
    getImage(path) {
      return require("../assets/images/avatar/" + path);
    },
  },
});
</script>
