import Vue from "vue";
import Vuex, { Store } from "vuex";
import { jsonService } from "../services/jsonService";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    allUser: [],
    currentUser: {},
    quizz: {},
    fiche: {},
    quizzCat : {},
    cas: {}
  },
  mutations: {
    SET_USERS(state, payload) {
      state.allUser = payload;
    },
    SET_CURRENT_USER(state, payload) {
      state.currentUser = payload;
    },
    SET_QUIZZ(state, payload) {
      state.quizz = payload;
    },
    SET_QUIZZ_CAT(state, payload) {
      state.quizz = payload;
    },
    SET_FICHE(state, payload) {
      state.fiche = payload;
    },
    SET_CAS(state, payload) {
      state.cas = payload;
    }
  },
  actions: {
    LOAD_USERS(store, path) {
      jsonService.getAll(path).then((result) => {
        store.commit("SET_USERS", result);
      });
    },
    LOAD_CURRENT_USER(store, who) {
      jsonService.getOne(who).then((result) => {
        store.commit("SET_CURRENT_USER", result);
      });
    },
    LOAD_QUIZZ(store, id) {
      jsonService.getQuizz(id).then((result) => {
        store.commit("SET_QUIZZ", result);
      });
    },
    LOAD_FICHE(store, id) {
      jsonService.getFiche(id).then((result) => {
        store.commit("SET_FICHE", result);
      });
    },
    LOAD_CAS(store, id) {
      jsonService.getCas(id).then((result) => {
        store.commit("SET_CAS", result)
      })
    }
  },
});
