


























































import Vue from "vue";
import Header from "@/components/Header.vue";
import { mapState } from "vuex";
import store from "@/store/index";
import { mdiPlus } from "@mdi/js";
export default Vue.extend({
  name: "ListCasClinique",
  components: {
    Header,
  },
  data() {
    return {
      who: undefined,
      show: false,
      show1: false,
      show2: false,
      avatarImg: require("../assets/images/avatar1.png"),
      casImg: require("../assets/images/cas-clinique.svg"),
      plusIcon: mdiPlus,
    } as any;
  },
  computed: mapState({
    getUser: (state: any) => state.currentUser,
    getcas: (state: any) => state.cas,
  }),
  mounted() {
    store.dispatch("LOAD_CURRENT_USER", this.who);
    store.dispatch("LOAD_CAS", this.who);
  },
  created() {
    const SegmentStr = window.location.pathname;
    const segmentArray = SegmentStr.split("/");
    const lastSegment = segmentArray.pop();
    this.who = lastSegment;
  },
  methods: {
    getImage(path) {
      return require("../assets/images/avatar/" + path);
    },
    goQuizz() {
      window.open("http://www.google.com");
    },
  },
});
