import myData from "../assets/json/newData.json";
import * as dot from "dot-object";

class ImportData {
  // get user

  async getAll(path: string) {
    try {
      const value = await dot.pick(path, myData);
      return value;
    } catch (err) {
      console.log("fetch failed", err);
    }
  }

  async getOne(who: string) {
    try {
      let value;
      for (let i = 0; i < myData.users.length; i++) {
        if (myData.users[i].id === who) {
          value = await myData.users[i];
        }
      }
      return value;
    } catch (err) {
      console.log("Something went wrong...", err);
    }
  }

  // get quizz

  async getQuizz(id: string) {
    try {
      let value;

      for (let i = 0; i < myData.users.length; i++) {
        if (myData.users[i].id === id) {
          value = await myData.users[i].content.quizz.content;
        }
      }
      return value;
    } catch (err) {
      console.log("Something went wrong...", err);
    }
  }

  getQuizzCat(id: string, cat: string) {
    try {
      let value;
      let element;
      let finalRes;
      for (let i = 0; i < myData.users.length; i++) {
        if (myData.users[i].id === id)
          for (element in myData.users[i].content.quizz.content) {
            value = Object.values(myData.users[i].content.quizz.content);
            for (let y = 0; y < value.length; y++) {
              const result = value[y].content;
              for (let x = 0; x < result.length; x++) {
                if (cat == result[x].quizzCat) {
                  finalRes = result[x];
                  //  console.log('Match => '+ result[x].name)
                }
              }
            }
          }
      }
      return finalRes;
    } catch (err) {
      console.log("Something went wrong...", err);
    }
  }

  // get Fiche
  async getFiche(id: string) {
    try {
      let value;
      for (let i = 0; i < myData.users.length; i++) {
        if (myData.users[i].id === id) {
          value = await myData.users[i].content.fiche.content;
        }
      }
      return value;
    } catch (err) {
      console.log("Something went wrong... ", err);
    }
  }

  // get Cas Clinique =>
  async getCas(id: string) {
    try {
      let value;
      for (let i = 0; i < myData.users.length; i++) {
        if (myData.users[i].id === id) {
          value = await myData.users[i].content.clinique.content;
        }
      }
      console.log(value);
      return value;
    } catch (err) {
      console.log("Something went wrong...", err);
    }
  }
}

export const jsonService = new ImportData();
