
















import Vue from 'vue';

export default Vue.extend({
  name: 'App',
  components: {  },
  data() {
    return {
      accueil: true,
      backBtn: require("./assets/images/caret-left-solid.svg"),
    }
  },
  methods:{
    back(){
      this.$router.go(-1);
    }
  },
  watch: {
    $route (to, from){
      console.log(to.name);
      console.log(from.name);
        if(to.name == null){
          this.accueil = true;
        }
        else if(to.name == "Home"){
          this.accueil = true;
        }
        else if(to.name == "Influenceur"){
          this.accueil = false;
        }
        else{
          this.accueil = false;
        }
    }
  }
});
