import Vue from "vue";
import VueRouter, { RouteConfig } from "vue-router";
import Home from "../views/Home.vue";
import Influenceur from "../views/Influenceur.vue";
import Quizz from "@/components/Quizz.vue";
import FicheList from "@/components/FicheList.vue";
import item350 from "@/components/CasClinique/item350.vue";
import item262 from "@/components/CasClinique/item262.vue";
import item265 from "@/components/CasClinique/item265.vue";
import ListCasClinique from "@/components/ListCasClinique.vue";
import Live from "@/components/Live.vue";
import QuizzList from "@/components/QuizzList.vue";
Vue.use(VueRouter);

const routes: Array<RouteConfig> = [
  {
    path: "",
    name: "Home",
    component: Home,
  },
  {
    path: "/influenceur/:id",
    name: "Influenceur",
    component: Influenceur,
  },
  {
    path: "/quizz/:qid/:id",
    name: "Quizz",
    component: Quizz,
  },
  {
    path: "/quizzList/:id",
    name: "Quizz List",
    component: QuizzList,
  },
  {
    path: "/fiche/:id",
    name: "Fiche List",
    component: FicheList,
  },
  {
    path: "/cas/influ2/item350",
    name: "Item 350",
    component: item350,
  },
  {
    path: "/cas/influ2/item262",
    name: "Item 262",
    component: item262,
  },
  {
    path: "/cas/influ2/item265",
    name: "Item 265",
    component: item265,
  },
  {
    path: "/cas/:id",
    name: "ListCasClinique",
    component: ListCasClinique,
  },
  {
    path: "/live/:id",
    name: "Live Page",
    component: Live,
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});
