







































































































































































































import Vue from "vue";
import Header from "@/components/Header.vue";
import { mapState } from "vuex";
import store from "@/store/index";
import { jsonService } from "@/services/jsonService";

export default Vue.extend({
  name: "Quizz",
  components: {
    Header,
  },
  data() {
    return {
      who: undefined,
      explainA: undefined,
      explainB: undefined,
      explainC: undefined,
      explainD: undefined,
      explainE: undefined,
      actualCat: undefined,
      data: undefined,
      responses: undefined,
      isHidden: true,
      verify: true,
      score: 0,
      totalScore: 0,
      color: "#808080",
      questionIndex: 0,
      progress: 0,
      progressUnit: undefined,
      avatarImg: require("../assets/images/avatar1.png"),
      quizzImg: require("../assets/images/quizz.svg"),
    } as any;
  },
  computed: {
    ...mapState({
      getUser: (state: any) => state.currentUser,
      getQuizz: (state: any) => state.quizz,
    }),
  },
  mounted() {
    store.dispatch("LOAD_CURRENT_USER", this.who);
    store.dispatch("LOAD_QUIZZ", this.who);
  },
  created() {
    // !! Un changement de route rends cette opération inopérante => à améliorer

    const SegmentStr = window.location.pathname;
    const segmentArray = SegmentStr.split("/");
    const lastSegment = segmentArray.pop();
    this.who = lastSegment;

    // !! Un changement de route rends cette opération inopérante => à améliorer

    const scdSegement = window.location.href.split("/");
    this.actualCat = scdSegement[4];
    this.data = jsonService.getQuizzCat(this.who, this.actualCat);
  },
  methods: {
    selectQuestion(params: any) {
      const element = document.getElementById(params);
      if (element!.classList.contains("selected")) {
        element!.classList.remove("selected");
      } else {
        element!.classList.add("selected");
      }
    },

    checkResp(data: any) {
      for (let i = 0; i < data.length; i++) {
        const element = document.getElementById(data[i].id);
        if (data[i].result == true) {
          this.totalScore++;
          if (element!.classList.contains("selected")) {
            element!.classList.remove("selected");
            element!.insertAdjacentHTML(
              "beforeend",
              '<i  v-show="questionIndex === questions.id"  class="fas fa-check ml-4 mb-2"></i>'
            );
            element!.classList.add("true");
            this.score = this.score + 1;
            console.log(this.score);
          } else {
            element!.classList.remove("selected");
            element!.classList.add("true");
          }
        } else if (data[i].result == false) {
          if (element!.classList.contains("selected")) {
            element!.classList.remove("selected");
            element!.insertAdjacentHTML(
              "beforeend",
              '<i class="fas fa-times ml-4 mb-2"></i>'
            );
            element!.classList.add("false");
          } else {
            element!.classList.remove("selected");
            element!.classList.add("false");
          }
        }
      }
    },
    reload() {
      location.reload();
    },
    getImg(path) {
      return require(`../assets/images/${path}`);
    },
    upProgress(unit) {
      this.progressUnit = unit;
      console.log(this.questionIndex);
      this.progress = this.progress + this.progressUnit;
      return this.progress;
    },
    downProgress(unit) {
      this.progress = this.progress - this.progressUnit;
      return this.progress;
    },
    getImage(path) {
      return require("../assets/images/avatar/" + path);
    },
    goThema(params) {
      this.$router.go(-1);
    },
  },
});
