


























































































import Vue from "vue";
import { mdiFacebook } from "@mdi/js";
import { mdiTwitter } from "@mdi/js";
import { mdiInstagram } from "@mdi/js";
import { mdiYoutube } from "@mdi/js";
import { mapState } from "vuex";
import store from "../store/index";

export default Vue.extend({
  name: "Influenceur",
  components: {
  },
  data() {
    return {
      who: undefined,
      fbIcon: mdiFacebook,
      bitchIcon: mdiInstagram,
      twitterIcon: mdiTwitter,
      youtubeIcon: mdiYoutube,
      avatarImg: require("../assets/images/avatar1.png"),
      ficheImg: require("../assets/images/fiches.svg"),
      quizzImg: require("../assets/images/quizz.svg"),
      casImg: require("../assets/images/cas-clinique.svg"),
      liveImg: require("../assets/images/live.svg"),
      errorIcon: require("../assets/icons/error-outline.svg"),
      showError: false
    } as any;
  },
  computed: mapState({
    user: (state: any) => state.currentUser,
  }),
  mounted() {
    store.dispatch("LOAD_CURRENT_USER", this.who);
  },
  methods: {
    getImage(path) {
      return require("../assets/images/avatar/" + path);
    },
    goSocial(param: any) {
      window.open(`${param}`);
    },
    goQuizzList() {
      if(this.user.content.quizz.isEmpty == true){
          this.showError = true;
          setTimeout(() => { this.showError = false }, 5000);
      }
      else{
        this.$router.push("/quizzList/" + this.who);
      }
    },
    goFiche() {
      if(this.user.content.fiche.isEmpty == true){
          this.showError = true;
          setTimeout(() => { this.showError = false }, 5000);
      }
      else{
        this.$router.push("/fiche/" + this.who);
      }
    },
    goCas() {
      if(this.user.content.clinique.isEmpty == true){
          this.showError = true;
          setTimeout(() => { this.showError = false }, 5000);
      }
      else{
        this.$router.push("/cas/" + this.who);
      }
    },
    goLive() {
       if(this.user.content.live.isEmpty == true){
          this.showError = true;
          setTimeout(() => { this.showError = false }, 5000);
      }
      else{
        this.$router.push("/live/" + this.who);
      }
    },
  },
  created() {
    const SegmentStr = window.location.pathname;
    const segmentArray = SegmentStr.split("/");
    const lastSegment = segmentArray.pop();
    this.who = lastSegment;
  },
});
