

















































































































































































































import Vue from "vue";
import Header from "@/components/Header.vue";
import { mapState } from "vuex";
import store from "@/store/index";
import { mdiPlus } from "@mdi/js";
import { mdiHeartPulse } from "@mdi/js";

export default Vue.extend({
  name: "quizz",
  components: {
    Header,
  },

  data() {
    return {
      who: undefined,
      show: false,
      avatarImg: require("../assets/images/avatar1.png"),
      quizzImg: require("../assets/images/quizz.svg"),
      plusIcon: mdiPlus,
      imgOph: require("../assets/icons/Ophtalmo.svg"),
      imgNep: require("../assets/icons/Nephrologie.svg"),
      imgDer: require("../assets/icons/Dermato.svg"),
      imgPne: require("../assets/icons/Pneumologie.svg"),
      imgRhu: require("../assets/icons/Rhumato.svg"),
      imgGyn: require("../assets/icons/Gyneco.svg"),
      imgPsy: require("../assets/icons/Psychatrie.svg"),
      imgNeu: require("../assets/icons/neuro.svg"),
      imgGas: require("../assets/icons/Gastro.svg"),
      imgPed: require("../assets/icons/baby.svg"),
      imgCar: mdiHeartPulse,
    } as any;
  },
  computed: mapState({
    getUser: (state: any) => state.currentUser,
    getQuizz: (state: any) => state.quizz,
  }),
  mounted() {
    store.dispatch("LOAD_CURRENT_USER", this.who);
    store.dispatch("LOAD_QUIZZ", this.who);
  },
  created() {
    const SegmentStr = window.location.pathname;
    const segmentArray = SegmentStr.split("/");
    const lastSegment = segmentArray.pop();
    this.who = lastSegment;
  },
  methods: {
    getImage(path) {
      return require("../assets/images/avatar/" + path);
    },
  },
});
